/** @jsx jsx */
import { jsx, css } from "@emotion/react"
import { useItemsState, useItemsDispatch } from "../../items/_state/hooks"
import { findDefaultSelectVal } from "Utils"
import Selects from "../selects"

function StorefrontPageSize() {
  const itemsState = useItemsState()
  const itemsDispatch = useItemsDispatch()

  const options = itemsState.settings.sortingOptionsPageSize

  function customOnChange(data) {
    itemsDispatch({
      type: "SET_QUERY_PARAMS",
      payload: {
        ...itemsState.queryParams,
        first: parseInt(data.value),
      },
    })

    itemsDispatch({ type: "SET_IS_FETCHING_NEW", payload: true })
  }

  return (
    <Selects
      dropzone={itemsState.settings.dropzonePageSize}
      labelText={itemsState.settings.pageSizeLabelText}
      selectId="swp-pagesize"
      options={options}
      isLoading={itemsState.isLoading}
      customOnChange={customOnChange}
      defaultValue={findDefaultSelectVal(options, itemsState.settings.pageSize)}
    />
  )
}

export default StorefrontPageSize
